import axios from 'axios';
const url = "http://localhost:5000"

const state = {
    user:{},
    isAuth:false,
    isLoading:false,
    errors:{},
}

const getters = {
    getUser: (state) => state.user,
    getIsAuth: (state) => state.isAuth,
    getIsLoading: (state) => state.isLoading,
    getUserErrors: (state) => state.errors
}

const actions = {
    async fetchUser({ commit }){
        try{
            const res = await axios.get(url+'/user/info');
            commit('fetchUser',res.data);
        }
        catch(err){
            console.log(err.response);
        }
    },
    async login({commit},loginData){
        try{
            const config = {headers:{'Content-type':'application/json'}}
            const body = JSON.stringify(loginData)
            const res = await axios.post(url+'/user/login',body,config);
            this.clearErrors();
            commit('login',res.data);
        }
        catch(err){
            if(err.response) commit('setErrors',err.response.data)
            console.log(err.response);
        }
    },
    async register({commit},registerData){
        try{
            const config = {headers:{'Content-type':'application/json'}}
            const body = JSON.stringify(registerData)
            const res = await axios.post(url+'/user/register',body,config);
            this.clearErrors();
            commit('register',res.data);
        }
        catch(err){
            commit('setErrors',err.response.data)
            console.log(err.response.data);
        }
    },
    clearErrors({commit}){
        commit('setErrors',{});
    },
    setErrors({commit},errors){
        commit('setErrors',errors);
    }
    
}

const mutations = {
    fetchUser: (state,user) => (state.user=user,state.isAuth=true,state.isLoading=false),
    login: (state,user) => (state.user=user,state.isAuth=true,state.isLoading=false),
    register: (state,user) => (state.user=user,state.isAuth=true,state.isLoading=false),    
    setErrors: (state,errors) => (state.errors = errors)
}

export default {
    state,
    getters,
    actions,
    mutations
}